import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatButtonModule, MatGridListModule, MatIconModule, MatMenuModule, MatTooltipModule } from '@angular/material';
import { ThemePickerComponent } from './themePicker.component';
import { StyleManagerService } from './styleManager.service';
import { ThemeStorageService } from './themeStorage.service';

@NgModule({
    imports: [
        MatButtonModule,
        MatIconModule,
        MatMenuModule,
        MatGridListModule,
        MatTooltipModule,
        CommonModule
    ],
    exports: [ThemePickerComponent],
    declarations: [ThemePickerComponent],
    providers: [StyleManagerService, ThemeStorageService],
})
export class ThemePickerModule { }