import { Component, ViewEncapsulation, ChangeDetectionStrategy, OnInit } from '@angular/core';
import { StyleManagerService } from './styleManager.service';
import { ThemeStorageService, DocsSiteTheme } from './themeStorage.service';

@Component({
  selector: 'limber-theme-picker',
  templateUrl: 'themePicker.component.html',
  styleUrls: ['themePicker.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class ThemePickerComponent implements OnInit {
  currentTheme;
  
  themes = [
    {
      primary: '#673AB7',
      accent: '#FFC107',
      href: 'deeppurple-amber.css',
      isDark: false,
    },
    {
      primary: '#3F51B5',
      accent: '#E91E63',
      href: 'indigo-pink.css',
      isDark: false,
    },
    {
      primary: '#E91E63',
      accent: '#607D8B',
      href: 'pink-bluegrey.css',
      isDark: false,
    },
    {
      primary: '#9C27B0',
      accent: '#4CAF50',
      href: 'purple-green.css',
      isDark: false,
    },
  ];
  
  constructor(public styleManager: StyleManagerService, private _themeStorage: ThemeStorageService ) {
    const currentTheme = this._themeStorage.getStoredTheme();
    if (currentTheme) {
      this.installTheme(currentTheme);
    }
  }
  
  ngOnInit() {
    // this._themeStorage.clearStorage();
    // let themeCurrent = this._themeStorage.getStoredTheme();
    // if (themeCurrent) {
    //   this.styleManager.setStyle('theme', `assets/css/${themeCurrent.href}`);
    // } else {
    //   this.styleManager.setStyle('theme', `assets/css/indigo-pink.css`);
    // }
  }
  
  installTheme(theme: DocsSiteTheme) {
    this.currentTheme = this._getCurrentThemeFromHref(theme.href);
    
    if (theme.isDefault) {
      this.styleManager.removeStyle('theme');
    } else {
      this.styleManager.setStyle('theme', `assets/css/${theme.href}`);
    }
    
    if (this.currentTheme) {
      this._themeStorage.storeTheme(this.currentTheme);
    }
  }
  
  private _getCurrentThemeFromHref(href: string): DocsSiteTheme {
    return this.themes.find(theme => theme.href === href);
  }
}
